@import '../App.css';

.container {
    display: flex;
    height: 100%;
    width: 100%;
    padding: 2rem 3rem;
}
.col {
    display: flex;
    width: 50%;
}
.left {
    flex-direction: column;
    gap: 1.5rem;
}
.left span {
    font-size: 1.25rem;
}
.right {
    align-items: center;
    justify-content: center;
}
.buttons {
    padding: 2.5%;
    column-gap: 0.5rem;
    width: 100%;
    height: 10%;
    justify-content: flex-start;
    align-items: center;
}
.buttons button {
    width: auto;
}
.right img {
    border-radius: 250px;
    width: 100%;
    max-width: 450px;
}
.home .left {
    padding-left: 3rem;
    justify-content: center;
}
.home .left p {
    font-size: 2rem;
    width: 50%;
}
.home .left h3 {
    font-size: 1.25rem;
}
.typed {
    width: 60%;
    padding: 0.75rem;
    background-color: var(--grey-main);
    border-radius: 10px;
    color: var(--grey-sub2);
}
.resume {
    background-color: var(--matte-yellow);
    color: var(--grey-main);
}
.resume:hover {
    background-color: var(--light-purple);
}
@media all and (max-width: 646px) {
    .home .container {
        flex-direction: column-reverse;
        gap: 2rem;
        justify-content: center;
    }
    .typed {
        text-align: center;
    }

    .main .left {
        gap: 1rem;
        justify-content: center;
    }
    .right img {
        width: 50%;
    }
    .home .col {
        width: 100%;
        align-items: center;
        justify-content: center;
        padding: 0;
        
    }
    .home .left p{
        width: 100%;
        font-weight: 800;
        text-align: center;
    }.home .left h3 {
        text-align: center;
    }
    .home {
        height: 100vh;
    }
    .container .buttons {
        flex-direction: row !important;
        gap: 1rem;
        align-items: center;
        justify-content: center;
    }
}
