@import '../App.css';
.img-skill img {
    filter: brightness(0);
    column-gap: 1rem;
    height: auto;
    width: 100%;
  justify-content: space-between;
}
.marquee div {
    text-align: center;
    font-weight: 700;
}
.skills h3 {
    text-align: center;
    padding: 0.5rem;
    font-size: 1.5rem;
    
    font-weight: 800;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}
.marquee {
    padding: 2rem 2rem;
    overflow: hidden;
    margin: 0 5rem;
    background-color: #f3f3f3;
}
.skills {
    background-color: #f3f3f3;
}
.skill {
    padding: 5%;
    display: grid;
        grid-template-columns: repeat(6, auto);
    column-gap: 3rem;
}
.img-skill {
    width: 100%;
    display: flex;
    transition: 0.2s all ease-out;
    align-items: center;
    justify-content: center;
    border-radius: 15px;
    padding: 0.5rem;
    overflow: auto;
}

.img-skill:hover {
    -webkit-box-shadow: 0px 5px 0px 0px rgba(0,0,0,1);
-moz-box-shadow: 0px 5px 0px 0px rgba(0,0,0,1);
box-shadow: 0px 5px 0px 0px rgba(0,0,0,1);
}

@media all and (max-width: 832px) {
    .skills {
        padding: 5%;
    }
    .skill {
        grid-template-columns: repeat(3, auto);
    }
}