@import '../App.css';
#projects {
  height: 100vh;
}
 .portfolio {
    padding: 0;
    display: flex;
    
}
.projects .container {
  height: 100%;
  display: grid;
  grid-template-columns: 1fr;
  min-width: 0;
}
.portfolio h1 {
  font-size: 2.5rem;
}
.holder {
  padding: 1rem;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 40px;
    width: 100%;
    height: auto;
    
}

.projects {
    padding: 5%;
  }
  .heading {
    text-align: center;
  }

  @media all and (max-width: 958px) {
    #projects {
      height: auto;
    }
    .holder {
      height: auto;
    }
  }

  @media all and (max-width: 540px) {
    .holder {
      grid-template-columns: 1fr
    }
  }