@import "../App.css";
.card {
  width: 100%;
  padding: 1rem;
  background-color: #fff;
  border-radius: 5px;
  border: none;
  height: auto;
}
.projects .image {
  justify-content: center;
  

}
.image img {
  width: 100%;
}
.image {
  display: flex;
  flex-direction: column;
  align-items:end;
  gap: 0.5rem;
}
.projects h3, .projects h2, .projects p, .projects .content {
  padding: 0;
}
.projects {
  font-size: 1rem;
}
.projects h2 {
  font-size: 1.5rem;
}
.b {
  background-color: var(--grey-main);
  padding: 1%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.projects-button a {
  display: flex;
  align-items: flex-end;
  padding-top: 1%;
  gap: 0.5rem;
}