@import '../App.css';
.about .col {
    padding: 5rem;
}
.about .left {
    justify-content: center;
}
.about {
    height: auto;
}
.left img {
    height: 75%;
    aspect-ratio: 0.5;
}
.about .right {
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;
    height: auto;
    padding: 5%;
    gap: 0.5rem;
}

.right p span {
    font-weight: 700;
}
.asu {
    color: #8C1D40;
}
.about .right p {
    padding-top: 1rem;
}
@media all and (max-width: 832px) {
    .about .container {
        flex-direction: column;

    }
    .about .col {
        width: 100%;
    }
    .about {
        height: auto;
    }
    .left img {
        height: 5%;
    }
}
@media all and (min-width: 1136px) {
    .about .right {
        justify-content: center;
    }
}