@import '../App.css';
.logo {
    font-weight: 900;
    transition: 0.2s all;
    color: #fff;
    padding: 0.25rem 0.5rem;
    background-color: var(--grey-main);
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}
.logo:hover {
    color: var(--grey-main);
    background: none;
    border: 2px solid var(--grey-main);
}
.header-content {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1.5rem;
    font-size: 1.15rem;
    margin: 0 auto;
    height: 60px;
    position: fixed;
    background-color: #fff;
    z-index: 9999;
}
.home {
    padding-top: 60px;
}
header {
    
    width: 100%;
    background-color: #fff;
    z-index: 9999;
    
}
.nav-links {
    display: flex;
    column-gap: 1.25rem;
    
}
.nav-link {
    transition: 0.2s all;
    
}
.nav-link:hover, .active-link {
    color: var(--matte-red);

}
nav {
    display: flex;
    column-gap: 1rem;
    align-items: center;
    font-weight: 600;
}
.button {
    padding: 0.5rem;
    border-radius: 10px;
    border: 1px solid transparent;
    font-weight: 600;
    font-size: 1.15rem;
    cursor: pointer;
    background-color: var(--grey-main);
    color: white;
    transition: 0.2s all;
}
.button:hover {
    background-color: var(--matte-red);
}
.shadow-header {
    box-shadow: 0 1px 16px hsla(0, 0%, 0%, .12);
}

.hamburger {
    display: none;
    height: fit-content;
    cursor: pointer;
    padding: 3px 8px;
    border-radius: 5px;
    transition: 0.2s;
    z-index: 9999;
}
.hamburger:hover {
    background: #fff;
}
.hamburger div {
    width: 30px;
    height: 2px;
    margin: 6px 0;
    background: var(--grey-main);
}
@media all and (max-width: 646px) {
    nav {
        position: absolute;
        right: -300px;
        top: 0;
        width: 280px;
        height: 100vh;
        background-color: #fff;
        transition: 0.2s;
        flex-direction: column;
        padding: 5%;
        justify-content: center;
    }
    .show-menu {
        right: 0;
    }
    nav ul {
        flex-direction: column;
        padding: 0 20px;
        margin-top: 30px;
        justify-content: center;
        height: 100%;
        gap: 10%;
    }
    nav ul li a {
        margin-bottom: 5px;
        padding: 10px 15px;
        border-radius: 5px;
    }
    .hamburger {
        display: block;
    }
}