@import url('https://fonts.googleapis.com/css2?family=Quicksand&display=swap');
:root {
  --opaque-blue: #e0fbfc;
  --blue-grey: #c2dfe3;
  --grey-main: #253237;
  --grey-sub1: #5c6b73;
  --grey-sub2: #9db4c0;
  --light-purple: #cfc2ff;
  --matte-red: #ef2e3b;
  --matte-yellow: #ffbb33;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
html {
  scroll-behavior: smooth;
}

body {
  font-family: 'Quicksand';
}

a {
  text-decoration: none;
  color: inherit;
}

ul {
  list-style: none;
}
section {
  height: 100vh;
  width: 100vw;
}
section:nth-child(odd) {
  background-color: #f3f3f3;
}
div {
  width: 100%;
}