@import '../App.css';
.contact {
    padding: 5%;
}
.icons {
    padding: 1% 0;
    display: flex;
    gap: 0.5rem;
}
.connect h3 {
    font-size: 1rem;
}
.icon {
    padding: 1%;
    background-color: var(--grey-main);
    border-radius: 10px;
    border: none;
    
}
.icon:hover {
    background-color: var(--matte-red);
    transition: 0.2s all ease-in-out;
    transform: translateY(2.5px);
}
form {
    color: white;
    display: grid;
    grid-template-columns: repeat(2, auto);
    width: 100%;
    column-gap: 1rem;
    row-gap: 0.75rem;
    overflow: none;
    height: auto;
}
.right .button {
    background-color: var(--matte-yellow);
    color: var(--grey-main);
    width: 100%;
    
}
.right .button:hover {
    background-color: var(--opaque-blue);
}
.w-100 {
    grid-column: span 2;
}
.contact .left {
    padding-top: 5%;
}
.contact .left h1 {
    font-size: 2rem;
}
.bottom {
    inline-size: 60%;
    display: grid;
    gap: 0.5rem;
}
.contact .right {
    padding: 5%;
    background-color: black;
    color: #fff;
    border-radius: 10px;
    border: none;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    gap: 1.5rem;
}
.input__wrap {
    display: flex;
    flex-direction: column;
    height: max-content;
    gap: 0.5rem;
}
.input {
    padding: 5%;
    border-radius: 5px;
    background-color: transparent;
    border: 1px solid white;
    color: white;
}
#ta {
    height: 150px;
    resize: vertical;
    min-height: 80px;
    max-height: 272px;
}
.button:disabled, .button:disabled:hover {
    background-color: var(--matte-red);
}

@media all and (max-width: 832px) {
    .contact {
        height: auto;
    }
    .contact .container {
        flex-direction: column;
        gap: 1rem;
    }
    .contact .col {
        width: 100%;
    }
    .bottom {
        padding: 1%;
    }
    .input {
        padding: 2.5%;
    }
}
@media all and (max-width: 427px) {
    form {
        display: flex;
        flex-direction: column;
        gap: 0.5rem;
    }
    .contact {
        height: auto;
    }
}